import React, { Component } from 'react'
import { Tab } from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css';
import './App.scss'

export default class App extends Component {

  panes = [
    { menuItem: 'Grade Point Average', render: () => <Tab.Pane attached={false}>Tab 1 Content</Tab.Pane> },
    { menuItem: 'Cumulative Grade Point Average', render: () => <Tab.Pane attached={false}>Tab 2 Content</Tab.Pane> },
  ]

  render() {
    return (
      <div className='topContainer'>
        <div className='heading'>
          <div className='image'></div>
          <div className='content'>
            <h1>CGPA Calculator</h1>
            <span>University of Karachi</span>
          </div>
        </div>
        <div className="main">
          <Tab menu={{ pointing: true }} panes={this.panes} />
        </div>
        <div className="footer">
          <span>Calculations are performed by the equations and table provided on <a rel="noopener noreferrer" href="http://www.uok.edu.pk/sem_results/rules.php" target='_blank'>Official Website</a></span>
        </div>
      </div>
    )
  }
}
